/**
 * Sticky footers
 */

 html {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

body {
  min-height: 100%;
  padding-bottom: 10rem;
  position: relative;
}

/**
 * Spectre overrides
 */

body {
  font-family: Lato, "Open Sans", sans-serif;
}



